import React, { Component } from 'react';
import './homepage.css';
import PageCover from '../../Presentation/PageCover/pagecover';
import HomeMain from './../../Presentation/HomeMain/homemain';
import Helmet from 'react-helmet';
import picture from '../../Presentation/PageCover/picture.png';

export class HomePage extends Component {
    render() {
        return (
            <div id="home-page-holder">
                <Helmet>
                    <meta
                        name="description"
                        content="Rodrigo Rocha - página e blog pessoal"
                    />
                    <title>Rodrigo Rocha</title>
                    <meta property="og:site_name" content="Rodrigo Rocha"></meta>
                    <meta property="og:description" content="Blog e página pessoal" ></meta>
                    <meta property="og:title" content="Rodrigo Rocha" ></meta>
                    <meta property="og:type"  content="website" ></meta>
                    <meta property="og:url"   content="https://rodrigogomesrc.github.io"></meta>
                    <meta property="og:image" content={picture} ></meta>
                    <meta property="og:image:alt"    content="Author Picture" ></meta>
                    <meta property="og:image:type"  content="image/png" ></meta>
                    <meta property="og:locale" content="pt_BR" ></meta>
                    <meta property="og:locale:alternate" content="es_US" ></meta>
                </Helmet>
               <PageCover language={this.props.language}></PageCover>
               <HomeMain language={this.props.language}></HomeMain>
            </div>
        )
    }
}

export default HomePage
